<template>
  <v-app>
    <v-container>
      <Header
        :linkSlider="linkSlider"
        :categories="categories"
        :order="order"
        v-on:open-shopping-cart="shoppingCartStatus = true"
      />

      <Dishes :data="data" :order="order" v-on:add-dish="addDish($event)" />
    </v-container>

    <ShoppingCart
      :status="shoppingCartStatus"
      :order="order"
      :orderTotal="orderTotal"
      v-on:add-dish="addDish($event)"
      v-on:remove-dish="removeDish($event)"
      v-on:close-shopping-cart="shoppingCartStatus = false"
    />
  </v-app>
</template>

<script>
import Header from "./components/Header";
import Dishes from "./components/Dishes";
import ShoppingCart from "./components/ShoppingCart";

export default {
  name: "App",

  components: {
    Header,
    Dishes,
    ShoppingCart,
  },

  data: () => ({
    tick: false,
    linkSlider: 0,
    data: null,
    shoppingCartStatus: false,
    categories: [
      { _id: "1", name: "Polievky", rank: 1 },
      { _id: "12", name: "Hlavné jedlá", rank: 2 },
      { _id: "123", name: "Pizza", rank: 3 },
      { _id: "1234", name: "Prílohy", rank: 4 },
      { _id: "12345", name: "Nápoje", rank: 5 },
    ],
    dishes: [
      {
        id: "01",
        category: "1",
        name: "Slepačí vývar",
        amount: "330ml",
        price: 2.95,
        imageUrl: "#",
        description:
          "Čerstný slepačí vývar s mäsom a rezancami",
      },
      {
        id: "011",
        category: "1",
        name: "Rajčinová",
        amount: "330ml",
        price: 2.95,
        imageUrl: "#",
        description: "Rajčinová polievka s kúskamy rajčín a bazalkou",
      },
      {
        id: "02",
        category: "12",
        name: "Vyprážaný syr s hranolkami",
        amount: "200g + 150g",
        price: 6.95,
        imageUrl: "#",
        description: "Vyprážaný syr Eidam s hranolkami a zeleninou",
      },
      {
        id: "03",
        category: "123",
        name: "Hawai",
        amount: "550g",
        price: 4.95,
        imageUrl: "#",
        description: "Chrumkavá pizza priamo z pece",
      },
      {
        id: "033",
        category: "123",
        name: "Kukuricová",
        amount: "575g",
        price: 5.45,
        imageUrl: "#",
        description: "Chrumkavá pizza priamo z pece",
      },
      {
        id: "0333",
        category: "123",
        name: "4 druhy syra",
        amount: "600g",
        price: 6.15,
        imageUrl: "#",
        description: "Chrumkavá pizza priamo z pece",
      },
      {
        id: "04",
        category: "1234",
        name: "Varené zemiaky",
        amount: "200g",
        price: 1.95,
        imageUrl: "#",
        description: "Varené zemiaky s maslom",
      },
      {
        id: "05",
        category: "12345",
        name: "Kofola",
        amount: "300ml",
        price: 0.95,
        imageUrl: "#",
        description: "Čapovaná kofola",
      },
      {
        id: "055",
        category: "12345",
        name: "Minerálka Rajec",
        amount: "500ml",
        price: 0.75,
        imageUrl: "#",
        description: "Jemne perlivá minerálka Rajec",
      },
    ],
    order: [],
  }),
  computed: {
    orderTotal: function () {
      let total = 0;
      this.order.forEach((dish) => {
        total += Number(dish.price * dish.quantity);
      });
      return total.toFixed(2);
    },
  },
  mounted: function () {
    // fetch("http://localhost:5000/api/restaurant/menu/5d8fb51f0b13652904156eb8")
    //   .then((response) => response.json())
    //   .then((data) => {
    //     this.categories = data.categories;
    //     this.dishes = data.dishes;
    //     this.generateData();
    //   });

    this.generateData();

    window.addEventListener("scroll", () => {
      if (!this.tick) {
        setTimeout(
          function () {
            this.updateActiveCategoryLink();
            this.tick = false;
          }.bind(this),
          100
        );
      }
      this.tick = true;
    });
  },
  methods: {
    generateData: function () {
      this.categories = this.categories.sort((a, b) => {
        return a.rank - b.rank;
      });

      let data = this.categories;

      data.forEach((e) => {
        e.items = [];
      });

      this.dishes.forEach((dish) => {
        const index = data.findIndex(
          (category) => category._id === dish.category
        );
        if (index > -1) {
          data[index].items.push(dish);
        }
      });

      this.data = data;
    },
    addDish: function (dish) {
      const index = this.order.findIndex((item) => item.id === dish.id);
      if (index > -1) this.order[index].quantity++;
      else
        this.order.push({
          id: dish.id,
          name: dish.name,
          description: dish.description,
          price: dish.price,
          quantity: 1,
        });
    },
    removeDish: function (dish) {
      const index = this.order.findIndex((item) => item.id === dish.id);
      if (dish.quantity > 1) this.order[index].quantity--;
      else this.order.splice(index, 1);
    },
    updateActiveCategoryLink: function () {
      let mainNavLinks = document.querySelectorAll(".category-link");
      // let mainSections = document.querySelectorAll(".category");
      let fromTop = window.scrollY;

      mainNavLinks.forEach((link, i) => {
        let section = document.getElementById(link.hash.split("#")[1]);

        if (
          section.offsetTop <= fromTop &&
          section.offsetTop + section.offsetHeight > fromTop
        ) {
          this.linkSlider = i;
          // link.childNodes[0].classList.add("custom-blue--text");
        } else {
          // this.linkSlider = i
          // link.childNodes[0].classList.remove("custom-blue--text");
        }
      });
    },
  },
};
</script>

