<template>
  <div>
    <div v-for="category in data" v-bind:key="category._id">
      <div class="category" v-bind:id="category._id">
        <v-card-title
          class="headline font-weight-black"
          style="padding-left: 0; padding-bottom: 0"
        >
          {{ category.name }}
        </v-card-title>
        <v-expansion-panels accordion>
          <v-expansion-panel
            v-for="(dish, i) in category.items"
            :key="i"
            active-class="active"
          >
            <v-expansion-panel-header hide-actions>
              <v-col
                cols="1"
                sm="3"
                lg="2"
                class="hidden-xs-only image-thumbnail"
              >
                <v-img
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="125"
                  width="100%"
                  src="https://i.imgur.com/biNJ06H.jpg"
                ></v-img>
              </v-col>
              <v-col cols="12" sm="9">
                <v-card-title style="padding-left: 0px; padding-top: 0px"
                  ><span
                    v-if="order.findIndex((item) => item.id === dish.id) > -1"
                    style="color: #0360ec; margin-right: 8px"
                    >{{
                      order[order.findIndex((item) => item.id === dish.id)]
                        .quantity
                    }}x</span
                  >{{ dish.name }}</v-card-title
                >
                <v-card-subtitle style="padding-left: 0px"
                  ><span class="full-description">{{ dish.description }}</span>
                  <v-list-item-subtitle class="short-description">{{
                    dish.description
                  }}</v-list-item-subtitle>
                </v-card-subtitle>
                <v-card-text style="padding-left: 0px; padding-bottom: 0px">
                  <span class="font-weight-bold" style="padding-bottom: 0px"
                    >{{ dish.price }}€</span
                  >&#160;/{{ dish.amount }}
                  <v-btn
                    text
                    rounded
                    color="#0360ec"
                    small
                    @click.native.stop="$emit('add-dish', dish)"
                  >
                    Pridať
                  </v-btn>
                </v-card-text>
              </v-col>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-col cols="12" style="padding-top: 0px">
                <v-img
                  class="d-sm-none"
                  style="border-radius: 8px; margin-top: 4px"
                  max-height="250"
                  src="https://i.imgur.com/biNJ06H.jpg"
                ></v-img>
                <v-card-text class="hidden-xs-only" style="padding: 0px">
                  {{ dish.description }}
                </v-card-text>
              </v-col>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Dishes",
  props: ["data", "order"],
  data: () => ({}),
};
</script>

<style>
.image-thumbnail {
  padding-right: 12px !important;
}

.v-expansion-panel:before {
  box-shadow: none !important;
}

.full-description {
  display: none;
}

@media (max-width: 600px) {
  .v-expansion-panel-header {
    padding: 0 !important;
    padding-left: 8px !important;
    padding-right: 8px !important;
  }
  .v-expansion-panel-content__wrap {
    padding: 0 !important;
    padding-left: 8px !important;
    padding-right: 8px !important;
  }

  .active > * .full-description {
    display: block !important;
  }

  .active > * .short-description {
    display: none !important;
  }
}

@media (min-width: 600px) {
  .v-expansion-panel-header {
    padding: 16px 16px !important;
  }

  .v-expansion-panel-content__wrap {
    padding-left: 16px !important;
    padding-right: 16px !important;
  }
}
</style>